//ì¹´ìš´íŠ¸ë‹¤ìš´
const countDown = new Date("May 30, 2023 22:22:00").getTime();

const x = setInterval(function () {

  let now = new Date().getTime();

  let timer = countDown - now;

  let days = Math.floor(timer / (1000 * 60 * 60 * 24));
  let hours = Math.floor((timer % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  let minutes = Math.floor((timer % (1000 * 60 * 60)) / (1000 * 60));
  let seconds = Math.floor((timer % (1000 * 60)) / 1000);
  document.getElementById("date").style.fontSize = '50px';
  document.getElementById("date").innerHTML = days + "ì¼ " + hours + "ì‹œ " +
    minutes + "ë¶„ " + seconds + "ì´ˆ ";

  if (timer < 0) {
    clearInterval(x);
    document.getElementById("date").innerHTML = "ë“œë””ì–´ ì¶œì‹œë˜ëŠ” FILA ì—ë””ì…˜!";
  }
}, 1000);


